body {
  background: #31343b;
  color: #eee;
}

.top {
  margin-top: 40px;
}

svg {
  position: absolute;
}

.mouse-tile {
  fill: none;
  pointer-events: all;
}

.mouse-tile:hover {
  fill: rgba(255, 255, 255, 0.2);
  cursor: pointer;
}

.attack {
  width: 80px;
  height: 60px;
  border: #384967 solid 1px;
  text-align: center;
  vertical-align: middle;
  line-height: 60px;
  cursor: pointer;
}

.attack:hover {
  background-color: rgba(255, 255, 255, 0.1);
  border-color: #384967;
}
